<template>
  <div id="app">
    <div class="header">
      <ul class="header-button-left" v-if="step!=0">
        <li>Cancel</li>
        <!-- 누르면 뒤로가기 -->
      </ul>

      <ul class="header-button-right" v-if="step==1" v-on:click="step=2">
        <li>Next</li>
        <!-- 누르면 다음 동작으로 변경 -->
      </ul>
      <ul class="header-button-right" v-if="step==2" v-on:click="publish">
        <li>Publish</li>
      </ul>

      <img src="./assets/logo.png" class="logo" />
    </div>

    <Body v-on:text="uploadCaption=$event" :posts="posts" :step="step" :uploadImages="uploadImages" />
    <!-- App.vue에서 Body.vue로 posts 데이터를 넘겨주는 방법 -데이터바인딩 :posts="posts" -->

    <div class="footer">
      <ul class="footer-button-plus">
        <!-- <li>+</li> -->
        <input type="file" id="file" class="inputfile" v-on:change="upload" />
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>
  </div>
</template>

<script>
import Body from "./components/Body.vue";
import Posts from "./assets/postdata.js";

export default {
  name: "App",
  data() {
    return {
      // step0 : post.vue / step1: filter / step2: write
      posts: Posts,
      step: 1,
      uploadImages: '',
      uploadCaption:'',
    };
  },
  components: {
    Body
  },
  methods: {
    upload(e) {
      // upload 기능개발
      let file = e.target.files;
      let reader = new FileReader();
      reader.readAsDataURL(file[0]);
      reader.onload = e => {
      console.log(e.target.result);
      this.step = 1;  // this 꼭 써야한다
      this.uploadImages = e.target.result;
      };
    },
    publish() {
      // publish를 누르면 
      // 1. 메인화면으로 돌아간다. 
      // 2. 내가 작성한 글 업로드 이미지를 바탕으로 만들어서
      // @click="$emit('Button','hello world')"
      //  v-on:Button="say=$event"
      // 3. this.posts 맨앞에 추가함
      this.step = 0;
      var newUpload = {
        name:'aaa',
        userImage:"https://placeimg.com/100/100/arch",
        postImage:this.uploadImages,
        likes:100,
        date:'May 7',
        liked:false,
        caption:this.uploadCaption,
        filter:"claerdon"
      };
      this.Posts.unshift({newUpload}); 
    },
  }
};
</script>

